*{
  margin: 0;
  padding: 0;
}

.container {
  background-image: url('./assets/img/caminhada.jpg');
  background-size: cover;
  height: 100vh;
  border-radius: 10px;
  text-align: center;
  font-family: Arial, sans-serif;
}

.timer{
  background-color: rgba(255, 255, 255, 0.8);
  display: inline-block;
  padding: 100px;
  border-radius: 10px;
  margin-top: 50px;
}

.nav-bar {
  padding-top: 150px;
}

button {
  margin: 10px;
  padding: 10px 20px;
  font-size: 16px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: #4CAF50;
  color: white;
}

.calculo{
  display: block;
  background-color: rgba(255, 255, 255, 0.8);
  border: 2px solid #000;
}

input {
  margin: 10px;
  padding: 10px;
  font-size: 16px;
  border: 2px solid #000;
  border-radius: 5px;
}

@media (max-width: 1024px) {

  .timer {
    padding: 60px;
  }

  button {
    font-size: 15px;
    padding: 8px 18px;
  }

  .nav-bar {
    padding-top: 120px;
  }

}

/* Telas até 768px (tablets) */
@media (max-width: 768px) {

  .container {
    background-position: center;
  }

  .timer {
    width: 80%;
    padding: 40px;
  }

  .nav-bar {
    padding-top: 90px;
  }

  button {
    font-size: 14px;
    padding: 10px;
  }
}

/* Telas até 480px (celulares) */
@media (max-width: 480px) {

  .container {
    height: auto;
    padding-bottom: 50px;
  }

  .nav-bar {
    padding-top: 50px;
    display: flex;
    flex-direction: column;
    gap: 10px;
  }

  button {
    font-size: 14px;
    padding: 10px;
  }

  .timer {
    width: 90%;
    margin-top: 30px;
    padding: 30px;
  }

  .calculo {
    width: 90%;
    margin: auto;
    padding: 20px;
  }

  input {
    width: 90%;
    font-size: 14px;
  }
}